.app {
  color: red;
}

BODY {
  background-color: #cacaca;  
  user-select: none;
}

#gallery {
  margin-top: 3em;
  background-color: #cacaca;
  
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card {
  border: 1px solid #000;
  margin: 25% 2em;

  width: 85vw;
  min-width: 85vw;
  max-width: 85vw;

  height: 85vw;
  min-height: 85vw;
  max-height: 85vw;

  /* Center contents of .card both horizontally and vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

/**
 * There's an IMG inside a .card DIV, and we want the image to stretch as much
 * as it can within the DIV, but without stretching the DIV itself, and without
 * distorting the image.
 */
.card IMG {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
